import { Link, Outlet } from "react-router-dom";
import css from "./About.module.css";

function About() {
    return (
        <>
            {/*  CSS import  module 
                import   css.abc     . */}
            <div className={css.abc}>
                <p>/module CSS </p>
                <Outlet>    </Outlet>
            </div>

            <Link to="/about"></Link>
            <Link to="member"></Link>
            <Link to="location"></Link>
        </>
    );
}

export default About;
